<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <b-alert show><h3>글목록</h3></b-alert>
      </b-col>
    </b-row>
    <b-row class="mb-1 row justify-content-end">
      <b-col md="1.5">
        <b-form-select v-model="perPage" :options="options"></b-form-select>
      </b-col>
      <b-col md="1.5" class="text-right">
        <b-button variant="outline-primary" @click="moveWrite()"
          >글쓰기</b-button
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col v-if="articles.length">
        <b-table
          id="board-table"
          :items="articles"
          :fields="fields"
          :per-page="perPage"
          :current-page="currentPage"
          @row-clicked="viewArticle"
          hover
          responsive
          striped
          head-variant="dark"
        >
          <template #cell(subject)="data">
            <router-link
              :to="{
                name: 'boardDetail',
                params: { articleno: data.item.articleno },
              }"
              >{{ data.item.subject }}</router-link
            >
          </template>
          <template #cell(regtime)="data">
            {{ data.value | dateFormat }}
          </template>
        </b-table>
        <b-pagination
          v-model="currentPage"
          :total-rows="articles.length"
          :per-page="perPage"
          aria-controls="board-table"
          first-number
          last-number
        ></b-pagination>
      </b-col>
      <!-- <b-col v-else class="text-center">도서 목록이 없습니다.</b-col> -->
    </b-row>
  </b-container>
</template>

<script>
import { listArticle } from "@/api/board.js";
import moment from "moment";

export default {
  name: "BoardList",
  data() {
    return {
      articles: [],
      fields: [
        {
          key: "articleno",
          label: "글번호",
          tdClass: "tdClass",
          sortable: true,
        },
        { key: "subject", label: "제목", tdClass: "tdSubject", sortable: true },
        { key: "userid", label: "작성자", tdClass: "tdClass", sortable: true },
        { key: "regtime", label: "작성일", tdClass: "tdClass", sortable: true },
        { key: "hit", label: "조회수", tdClass: "tdClass", sortable: true },
      ],
      perPage: 5,
      currentPage: 1,
      options: [
        { value: 5, text: "5개" },
        { value: 10, text: "10개" },
        { value: 20, text: "20개" },
      ],
    };
  },
  created() {
    let param = {
      pg: 1,
      spp: 20,
      key: null,
      word: null,
    };
    listArticle(
      param,
      (response) => {
        this.articles = response.data;
      },
      (error) => {
        console.log(error);
      },
    );
  },
  methods: {
    moveWrite() {
      this.$router.push({ name: "boardRegister" });
    },
    viewArticle(article) {
      this.$router.push({
        name: "boardDetail",
        params: { articleno: article.articleno },
      });
    },
  },
  filters: {
    dateFormat(regtime) {
      return moment(new Date(regtime)).format("YY.MM.DD");
    },
  },
};
</script>

<style scope>
.tdClass {
  width: 50px;
  text-align: center;
}
.tdSubject {
  width: 300px;
  text-align: left;
}
</style>
